import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonHttpService } from './../../../../http/common.http.service';
import { BusinessRoleBean } from './../bean/business-role.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkName,checkRoleNum } from 'app/@uisftech/common/service/validator';
// 添加
@Component({
    selector: 'business-role-add',
    styleUrls: ['business-role-add.scss'],
    templateUrl: 'business-role-add.html',
    providers: [Commfunc, ConfirmationService]
})

export class BusinessRoleAdd implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //校验
     userform: FormGroup;
    //实体
     businessRoleBean: BusinessRoleBean = new BusinessRoleBean();
    //提示信息
     msgs: Message[] = [];
    //业务角色关联方下拉框
     roleRelaOpt: any = [];

    constructor(
        public fb: FormBuilder,
        public commonHttpService: CommonHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'roleTypeId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/),Validators.required]),
            'description': new FormControl('',[Validators.required,Validators.maxLength(255)]),
            "roleTypeRel": new FormControl('',Validators.required)
        });

        //业务角色关联方
        let param = { paramName: "roleType" }
        this.commonHttpService.queryCommonParamItemService(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.roleRelaOpt = this.commfunc.listToLabelvalue(data.returnCommonParamItemList, "paramItemValue", "paramItemName")
                this.roleRelaOpt.unshift({ label: "请选择", value: '' });
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //添加
    addSaveBtn() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //提交服务
        this.commonHttpService.createSpBusinessRole(this.businessRoleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.businessRoleBean = new BusinessRoleBean();
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        })
    }

    //取消
    addCloseBtn() {
        this.outValue.emit(false);
    }
}