import { Component, OnInit } from '@angular/core';
import { CommonHttpService } from './../../../http/common.http.service';
import { BusinessRoleBean } from './bean/business-role.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import {  tableMessage } from 'app/@uisftech/common/constant/config';
//业务角色管理
@Component({
  selector: 'app-business-role',
  templateUrl: './business-role.component.html',
  styleUrls: ['./business-role.component.scss'],
  providers: [Commfunc, ConfirmationService]
})
export class BusinessRole implements OnInit {
  // 表格中显示的信息
  tabMesg: any = tableMessage;

  //表数据
  tableData: any = [];
  //bean
  businessRoleBean: BusinessRoleBean = new BusinessRoleBean();
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  first: number = 0;
  total: string; //总页数
  //提示信息
  msgs: Message[] = [];
  //业务角色关联关系下拉框
  roleRelaOpt: any = [];
  currentPage:any;//当前页
  rows:any;//当前显示条数

  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.businessRoleBean.pageSize = 10;//每页多少条
    this.businessRoleBean.pageNum = 1;//当前页
  }

  ngOnInit() {
    //业务角色关联方
    let param = { paramName: "roleType" }
    this.commonHttpService.queryCommonParamItemService(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.roleRelaOpt = this.commfunc.listToLabelvalue(data.returnCommonParamItemList, "paramItemValue", "paramItemName")
        this.roleRelaOpt.unshift({ label: "请选择", value: '' });
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    //调查询方法
    this.queryClick();

    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
        if (event && event.keyCode === 13) {
            _thiss.query();
        }
    }
  }

  //查询
  queryClick() {
    this.commonHttpService.querySpBusinessRole(this.businessRoleBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.businessRoleList;
        this.total = data.total + '';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  query() {
    this.first=0;
    this.businessRoleBean.pageNum=1;
    this.commonHttpService.querySpBusinessRole(this.businessRoleBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.businessRoleList;
        this.total = data.total + '';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //删除按钮事件
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { roleTypeId: item.roleTypeId };
        this.commonHttpService.removeSpBusinessRole(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            // this.first = 0;
            // this.businessRoleBean.pageNum = 1;//当前页
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }
  //新增按钮事件
  addClick() {
    this.headerTitle = '新增';
    this.display = true;
    this.showModel = '1';
  }
  //添加的回调
  addCall(param) {
    this.display = param;
    this.queryClick();
  }


  //修改按钮事件
  modifyClick(item) {
    this.headerTitle = '修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item.roleTypeId;
  }

  //修改的回调
  updateCall(param) {
    this.display = param;
    this.queryClick();
  }

  //分页
  paginate(event) {
    this.rows=event.rows;
    //每页显示的条数
    this.businessRoleBean.pageSize = this.rows;
    //当前页
    this.currentPage = event.page + 1;
    this.businessRoleBean.pageNum = this.currentPage;
    //调用查询的方法
    this.first = event.page * event.rows;
    this.queryClick();
  }

  //重置
  resetClick() {
    this.businessRoleBean = new BusinessRoleBean();
    this.businessRoleBean.pageNum = 1;
    this.businessRoleBean.pageSize = 10;
  }
  //按钮权限
permissionCheck(id){
  return	this.commfunc.permissionCheck(id)
  }
}


