import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonHttpService } from './../../../../http/common.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { BusinessRoleBean } from './../bean/business-role.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// 修改
@Component({
    selector: 'business-role-modify',
    styleUrls: ['business-role-modify.scss'],
    templateUrl: 'business-role-modify.html',
    providers: [Commfunc, ConfirmationService]
})

export class BusinessRoleModify implements OnChanges {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //校验
     userform: FormGroup;
    //提示信息
     msgs: Message[] = [];
    //实体
     businessRoleBean: BusinessRoleBean = new BusinessRoleBean();

    constructor(
        public fb: FormBuilder,
        public commonHttpService: CommonHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'roleTypeId': new FormControl(''),
            'description': new FormControl('',[Validators.required,Validators.maxLength(255)]),
        });
    }

    ngOnChanges() {
        let param = { roleTypeId: this.inValue, flag: '0' };//flag为0的时精确查询
        this.commonHttpService.querySpBusinessRole(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.businessRoleBean = data.businessRoleList[0];
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });
    }

    //提交
    modifySaveBtn() {
        
        // 校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        //提交服务
        this.commonHttpService.updateSpBusinessRole(this.businessRoleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });

    }

    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
    }
}